import {MetricsPanelCtrl} from 'app/plugins/sdk';
import moment from 'moment';
import _ from 'lodash';
import TimeSeries from 'app/core/time_series';
import './css/clock-panel.css!';
import $ from 'jquery';

var panelDefaults = {
	modeType: 0
};

export class KDMSinglestatCtrl extends MetricsPanelCtrl {
  constructor($scope, $injector, $rootScope) {
    super($scope, $injector);
	this.$rootScope = $rootScope;
	_.defaults(this.panel, panelDefaults);
	var modeOption = [
      {name: '树(Tree)', value: 0},
	  {name: '下拉框(select)', value: 1}
    ];
	this.panel.modeOption = modeOption;
	
	this.events.on('init-edit-mode', this.onInitEditMode.bind(this));
	this.events.on('data-received', this.onDataReceived.bind(this));
  }
  
  onInitEditMode() {
    this.addEditorTab('Options', 'public/plugins/grafana-kdm-select-tree/editor.html', 2);
  }
  
  onDataReceived(dataList) {
	this.srcData = dataList;
	this.render();
  }
  
  //link中起到监听作用
  onRender(){
  }
 
  link(scope, elem, attrs, ctrl) {
	ctrl.events.on('render', function() {
		
	});
  }
}

KDMSinglestatCtrl.templateUrl = 'module.html';
